import Vue from 'vue'
import Router from 'vue-router'
import igames from '@/components/igames'
import game from '@/components/game'
import team from '@/components/team'
import teams from '@/components/teams'
import video from '@/components/video'
import singin from '@/components/singin'
import singup from '@/components/singup'
import chatx from '@/components/chatx'


import Datetime from 'vue-datetime'
import 'vue-datetime/dist/vue-datetime.css'
Vue.use(Datetime)
Vue.extend({
  template: '<datetime/>',
  components: {
    datetime: Datetime
  }
});

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: __dirname,
  routes: [
    {
      path: '/chat',
      name: 'chatx',
      component: chatx
    },
    {
      path: '/singin',
      name: 'singin',
      component: singin
    },
    {
      path: '/singup',
      name: 'singup',
      component: singup
    },
    {
      path: '/nhl/:date?',
      name: 'nhl',
      component: igames
    },
    {
      path: '/teams',
      name: 'teams',
      component: teams
    },
    {
      path: '/:date?',
      name: 'igames',
      component: igames
    },
    {
      path: '/team/:id',
      name: 'team',
      component: team
    },
    {
      path: '/game/:id/:cdn?/:feed?',
      name: 'game',
      component: game
    },
    {
      path: '/video/:vid/:home/:away/:feed?',
      name: 'video',
      component: video
    }
  ]
})



