<template id="page-chat">
  <div class="card" style="margin: 0;">
    <div class="card-body" style="height:390px;padding-top:13rem" v-if="user == 'false'">
      <router-link to="/singin">
        <button class="bttn-material-flat bttn-xs bttn-warning bttn-no-outline" style="width: 100%;">Sign In</button>
      </router-link>
      <br><br>
      <router-link to="/singup">
        <button class="bttn-material-flat bttn-xs bttn-warning bttn-no-outline" style="width: 100%;">Sign Up</button>
      </router-link>
    </div>
    <div class="card-body" v-else>
      <div class="page-content messages-content">
        <div class="messages messages-auto-layout">
          <div v-for="msg in states.msgs" class="message message-sent message-first message-appear-from-bottom" :style="msg.type == 'received' ? 'text-align: left;' : 'text-align: right;'">
            <div class="message-name">{{msg.name}}</div>
            <div class="message-text">{{msg.text}}</div>
          </div>
        </div>
      </div>
      <hr>
      <div class="toolbar messagebar">
        <div class="toolbar-inner">
          <div class="form-row">
            <div class="col">
              <input type="text" class="form-control" v-model='text'></input>
            </div>
            <div class="col-2">
              <button v-on:click="onSend" class="bttn-material-flat bttn-xs bttn-warning bttn-no-outline">Send</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  </template>
  <script>
    var pubnub = new PubNub({
      publishKey: 'pub-c-e43dc8b7-775e-451e-89b9-95a9ed5b2c7f',
      subscribeKey: 'sub-c-ec9c0390-ec48-11e8-91a4-7e00ddddd7aa'
    })
    var states = {
      name: '',
      msgs: []
    }
  export default {
    name: 'chatx',
    data () {
      return {
        states:states,
        text:'',
        user: localStorage.user
      }
    },
    mounted(){
      let k = JSON.parse(localStorage.user)
      states.name = k.user
      this.initPubNub();
    },
    methods: {
      onSend: function(text) {
        text = this.text
        if (text.trim().length === 0)
        return;
        pubnub.publish({
          channel: 'streamrip',
          message: {
            text:text,
            name:states.name
          }
        });
        this.text=''
      },
      initPubNub: function() {
       pubnub.addListener({
           message: function(data) {
               var type = data.message.name == states.name ? 'sent' : 'received';
               var name = type == 'send' ? states.name : data.message.name;
               states.msgs.push({
                 name:name,
                 text:data.message.text,
                 type:type
               });
           }
       });
       pubnub.subscribe({
           channels: ['streamrip']
       });
       pubnub.history(
           {
               channel : 'streamrip',
               count : 20
           },
           function (status, response) {
               var history = response.messages;
               for (var i=0; i<history.length; i++) {
                   var type = history[i].entry.name == states.name ? 'sent' : 'received';
                   states.msgs.push({
                       name:history[i].entry.name,
                       text:history[i].entry.text,
                       type:type
                   });
               }
           }
       );
   }
    }
  }
  </script>
  <style scoped>
    .message-text {
      margin: 0;
    padding: 0;
    background: transparent;
    font-weight: 400;
    position: relative;
    font-family: inherit;
    padding: 5px 12px;
    overflow: hidden;
    border-width: 0;
    border-radius: 2px;
    background: rgba(180,180,180,0.1);
    color: #1d89ff;
    transition: all .25s cubic-bezier(.02,.01,.47,1);
    transform: translateZ(0);
    }
    .page-content {
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    box-sizing: border-box;
    height: 390px;
    position: relative;
    z-index: 1;
}
.messages.messages-auto-layout {
    width: 99%;
}
::-webkit-scrollbar {
  width: .6rem;
}
::-webkit-scrollbar-thumb {
    margin-left: 1rem;
      background: #555;
      box-shadow: 0 0 1px 1px hsla(0,0%,100%,.25);
      -webkit-transition: opacity .2s linear;
    background-clip: padding-box;
    border-radius: 7px;
    min-height: 10px;
    opacity: 0;
    position: absolute;
    right: 2px;
    transition: opacity .2s linear;
}
  </style>



