<template>
  <div>
    <div class="atom-spinner" id="loaderc" v-if='!renderPage'>
      <div class="spinner-inner">
        <div class="spinner-line"></div>
        <div class="spinner-line"></div>
        <div class="spinner-line"></div>
        <div class="spinner-circle">
          &#9679;
        </div>
      </div>
    </div>
    <div class="fancy-box-wrapper row" v-if='renderPage'>
    <div class="box-wrapper">
      <div class="bttn-warning">
        <h2>NHL TEAMS</h2><br/>
      </div>
      <div class="col-lg-3 col-md-4 col-sm-6" v-for='(team, index) in teams' v-if='renderPage' style="display: inline-block;">
        <router-link :to="{ name: 'team', params: { id: team.id }}">
          <div class="box">
              <img v-bind:src="GetLogo(team.abbreviation)+'&amp;h=75&amp;w=75'">
              <div class="title-wrapper">
                <div class="title">{{team.name}}</div>
              </div>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</div>
</template>
<script>
const axios = require('axios')
export default {
  name: 'teams',
  data () {
    return {
      teams:{},
      renderPage:false,
      user: localStorage.user
    }
  },
  mounted () {
    if(!this.user){
      this.$router.push('/singin')
    }
    else{
     this.Load()
   }
  },
  methods: {
    Load(){
      let vm = this
      if(localStorage.getItem('teams')){
        vm.teams = JSON.parse(localStorage.getItem('teams'))
        vm.renderPage = true
      }
      else{
        axios.get('https://statsapi.web.nhl.com/api/v1/teams')
          .then(function (res) {
            vm.teams = res.data.teams
            const gm = JSON.stringify(vm.teams)
            localStorage.setItem('teams',gm)
            vm.renderPage = true
          })
          .catch(function (err) {
            console.log(err)
          })
      }
    },
    GetLogo(x){
      return GetLogo(x);
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .fancy-box-wrapper .box-wrapper .box {
    width: 100%;
    height: 160px;
}
    .card .card-body .form-group {margin: 2rem 0;}
    button.bttn-float.bttn-xs.bttn-warning.my-4 {
    width: 32%;
    margin: 0.125rem;
    padding: 0.5rem;
    font-weight: 600;
    font-size: 13px;
    }
</style>



