<template>
  <div class="card">
    <div class="atom-spinner" id="loaderc" v-if='!renderPage'>
      <div class="spinner-inner">
        <div class="spinner-line"></div>
        <div class="spinner-line"></div>
        <div class="spinner-line"></div>
        <div class="spinner-circle">
          &#9679;
        </div>
      </div>
    </div>
    <div class="col-lg-6 mb-lg-auto" style="margin: 0 auto;" v-else>
      <div class="card-login">
        <div class="card-header bttn-warning" style="margin: 0 auto;margin-top:6%">
          <i class="material-icons" style="font-size: 7rem;">fingerprint</i>
          <h3>Sing In</h3>
        </div>
        <div class="card-body">
          <div class="form-group mb-3" v-if='failed'>
            <div class="alert alert-danger" role="alert">
              Failed !! Check your info
            </div>
          </div>
          <div class="form-group mb-3" v-if='error'>
            <div class="alert alert-danger" role="alert">
              Error !! Connection Failed
            </div>
          </div>
          <div class="form-group mb-3" v-if='success'>
            <div class="alert alert-success" role="alert">
              Success !! Redirecting Now
            </div>
          </div>
            <div class="form-group mb-3">
              <div class="input-group input-group-alternative">
                <div class="input-group-prepend">
                  <span class="input-group-text">
                      <i class="material-icons">email</i>
                  </span>
                </div>
                <input class="form-control" placeholder="Email" v-model="user.mail" type="email">
              </div>
            </div>
            <div class="form-group">
              <div class="input-group input-group-alternative">
                <div class="input-group-prepend">
                  <span class="input-group-text">
                      <i class="material-icons">lock</i>
                  </span>
                </div>
                <input class="form-control" v-model="user.psw" placeholder="Password" type="password">
              </div>
            </div>
            <br>
            <div class="text-center">
              <button v-on:click="login" class="bttn-material-flat bttn-xs bttn-warning bttn-no-outline">Sign In</button>
              <router-link to="/singup">
                <button class="bttn-material-flat bttn-xs bttn-warning bttn-no-outline">Sign Up</button>
              </router-link>
            </div>
            <br>
        </div>
      </div>
  </div>
</div>
</template>
<script>
  const axios = require('axios')
export default {
  name: 'singin',
  data() {
    return {
      failed: false,
      error:false,
      renderPage: false,
      success:false,
      user:{
        mail:"",
        psw:""
      }
    }
  },
  watch: {
  },
  mounted () {
    this.renderPage = true
  },
  methods:{
    login(){
      this.failed = false
      this.success = false
      this.error = false
      var bodyFormData = new FormData()
      bodyFormData.set("mail",this.user.mail)
      bodyFormData.set("psw",this.user.psw)
      axios({
        method: 'post',
        url: 'http://fedilabs.com/aoi/singin.php',
        data: bodyFormData,
        config: { headers: {'Content-Type': 'multipart/form-data' }}
    }).then(request => this.loginSuccessful(request))
      .catch(() => this.loginFailed())
    },
    loginSuccessful(res){
      if(res.data == 'error'){
        this.failed = true
      }
      else{
        this.success = true
        localStorage.user = JSON.stringify(res.data)
        this.$router.push('/')
      }
    },
    loginFailed(){
      this.error = true
    }
  }
}
</script>

<style>
#app {
}
</style>



